.topnav {
  overflow: hidden;
  background-color: #24645d;
  display: flex;
  width: 100%;
  margin: auto;
  
  
}
.push{
  margin: auto;
 
}


.topnav #a {
  float: left;
  display: block;
  color: #ffffff;
  text-align: center;
  padding: 10px 16px;
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
}

.topnav #a:hover {
 border-bottom: 4px solid #c44d4d;
 
}

.topnav div.active {
  background-color: #24645d;
  color: #ffffff;
  font-size: 25px;
  font-weight: bolder;
  margin-left: 2rem;

}

.topnav .icon {
  display: none;
  font-size: 26px;
  color: #fff;
}

@media screen and (max-width: 800px) {
  .topnav #a:not(:first-child) {display: none;}
  .topnav #a.icon {
    float: right;
    display: block;
  }.topnav{
      width: 100%;
  }
  
}

@media screen and (max-width: 800px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive #a {
    float: none;
    display: block;
    text-align: center;
    margin-top: 1rem;
  }
 
  #searchIcon{
      display: none;
  }
  #BagIcon{
      display: none;
  
  }
  #seperator{
      display: none;
  }
  .input{
      display: none;
  }
}

/* searchbar */


.box{
  position: relative;
  display: flex;
  justify-content: space-evenly;
  margin-top: 1rem;
}

.input {
  /* padding: 10px; */
  width: 40px;
  height: 38px;
  background: none;
  border: 0;
  border: 0px ;
  border-radius: 0px;
  box-sizing: border-box;
  font-size: 20px;
  outline: none;
  transition: .5s;
  padding-right: 1rem;
  font-weight: 100;
  margin-right: .8rem;
  
}
.box:hover input{
  width: 250px;
  background: #0000000f;
  border-radius: 2px;
 
}
.box #searchIcon{
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translate(-40%,-50%);
  font-size: 25px;
  color: #00000066;
  transition: .2s;
}
.box:hover #searchIcon{
 
  color: #0f8400;
}

