
.text-small {
    font-size: 0.9rem;
    
  }
  
  a {
    color: #fff;
    text-decoration: none;
    transition: all 0.3s;
  }
  
  a:hover, a:focus {
    text-decoration: none;
  }
  
  footer {
    background: #24645d;
    color: #fff;
  }
  .text-muted {
    --bs-text-opacity: 1;
    color: #ffffff!important;
}