.FrontContainer{
    width: 100%;
    height: 500px;
    background-color: #24645d;
}
.textBox{
    width: 50%;
    height: 400px;
    background-color: #24645d;
    float: left;
    color: #fff;
}
#MainHead{
    font-size: 50px;
    margin-top: 4rem;
    margin-left: 4rem;
}
#SubHead{
    font-size: 16px;
    margin-top: 1rem;
    margin-left: 4rem;
}
.GetStartedBtn{
    width: 195px;
    height: 50px;
    background-color: #24645d;
    border: 4px solid #3e7471f9;
    font-size: 16px;
    color: #ffffff77;
    font-weight: bold;
    border-radius: 50px;
    margin-left: 4rem;
    float: left;
    margin-top: 1rem;
}
.GetStartedBtn:hover{
    color: rgba(255, 255, 255, 0.752);
    border: 4px solid rgba(255, 255, 255, 0.877);
}
.watchalign{
    width: 160px;
    height: 50px;
    background-color: #24645d;
    float: left;
    margin-top: 1rem;
}
#MainPlaybtn{
    font-size: 50px;
    color: #ffffff77;
    margin-left: 1rem;
    
}
#WatchText{
    float: right;
    margin-left: -.8rem;
    font-size: 16px;
    color: #ffffff77;
    font-weight: bold;
    margin-top: 13px;
}
.ImageBox{
    width: 50%;
    min-height: 400px;
    height: auto;
    background-color:#24645d;
    float: left;
}
#ImgResize{
    width: 80%;
    margin-top: 4rem;
    margin-left: 4rem;
}

@media screen and (max-width: 1100px) {
    .textBox{
        width: 100%;
    }
    .ImageBox{
        width: 100%;
    }
    #MainHead{
        margin-top: 5rem;
        font-size: 50px;
        margin-left: 2rem;
        
    }
    #SubHead{
        font-size: 16px;
        margin: 2rem;
    }
}

.cardArea{
    background-color: transparent;
    margin-bottom: 3rem;
    width: 90%;
    height: 310px;
    margin-top: -6rem;
    margin-left: auto;
    margin-right: auto;
}
.card1{
    min-width: 21%;
    height: 210px;
    background-color: #00574d;
    float: left;
    margin: 1rem;
    box-shadow: 5px 10px 10px rgba(6, 46, 25, 0.375);
}
@media screen and (max-width: 1100px) {
    .card1{
        width: 46%;
        height: 200px;
        margin: .5rem;
    }
    .cardArea{
        width: 100%;
        margin: auto;
    }
}
#iconAlign{
    color: #ffffff77;
    font-size: 70px;
    margin-left: 35%;
    margin-top: 1rem;
}
#IconText{
    color: #ffffff77;
    font-size: 20px;
    text-align: center;
    font-weight: bold;
}
