.Topcontainer{
    width: 100%;
    height: 40px;
    background-color: #1e4543;
    color: #fff;

}
.navM-links {
    display: flex;
    list-style: none;
}


.navM-brand {
   
    font-size: 16px;
    font-family: 'Play', sans-serif;
    font-family: 'Vollkorn', serif;
}

.navM-link {
   
    text-decoration: none;
    font-size: 11px;
}

.navbarM {
    width: 95%;
    height: 7.5vh;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

}


@media screen and (max-width: 800px) {
    .Topcontainer,.navbarM {
        display: none;

    }
}
#muIcon{
    
    font-size: 20px;
    margin-left: 1rem;
    margin-bottom: .5rem;
   
}
#muIcon2{
    
    font-size: 14px;
   margin-left: .8rem;
}