.FQcontainer{
    width: 100%;
    height: 600px;
    background-color: transparent;
}
.FQleft{
    width: 30%;
    height: 400px;
    background-color: #fff;
    float: left;
}

.FQright{
    width: 70%;
    height: 600px;
    background-color: #fff;
    float: left;
}
@media screen and (max-width: 900px) {
    .FQleft{
        width: 100%;
    }
    .FQright{
        width: 100%;
    }
}
.Single{
    width: 90%;
    height: 60px;
    background-color: #fff;
    margin: .8rem;
    font-size: 22px;
    padding-left: 2rem;
    box-shadow: 5px 10px 10px rgba(6, 46, 25, 0.375);
}
#FQtext{
    font-size: 25px;
    margin-top: 6rem;
    margin-left: 4rem;
}
#FQtext1{
    font-size: 40px;
    margin-top: -1rem;
    margin-left: 4rem;
}

#FQtext2{
    font-size: 16px;
    margin: 1rem;
    margin-left: 4rem;
}

