.bgArea{
    width: 100%;
    height:600px ;
   
    
}
.CallImg{
    width: 80%;
    background:linear-gradient(rgba(67, 67, 67, 0.4), rgba(105, 105, 105, 0.468)), url('https://img.freepik.com/free-photo/mumbai-skyline-seen-from-marine-drive-south-mumbai_469504-11.jpg?w=1380&t=st=1671869588~exp=1671870188~hmac=58d242f7bcb777ad786c6e78c8d1fee48cdf723012a7f9f93867bc2efb03c58a') no-repeat center ;
    height: 450px;
    overflow: hidden;
    background-size: cover;
    margin-left: auto;
    margin-right:auto ;
    border-radius: 12px;
}
.LiveButton{
    
    border-radius: 50px;
    background-color: rgb(4, 72, 50);
    border: none;
    margin-left: 50%;
    margin-top: 3rem;
   

}
#CalltoActionText{
    color: #f6f6f6;
    margin: 3rem;
    font-size: 20px;
    margin-top: 15rem;
}
#actionBtn{
    width:180px;
    height: 50px;
    color: #f6f6f6;
    border: 4px solid #f6f6f6;
    border-radius: 25px;
    background-color: transparent;
    margin-left: 35%;
}